@import "../../../scss/variables";
@import "../../../scss/mixins";


.desktop-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-grow: 0;
    flex-shrink: 0;
    margin-top: 1rem;
    cursor: pointer;
    cursor: url("../../../assets/images/cursor-pointer.png"), pointer;
    user-select: none;
    width: 100%;
    height: 6rem;

    &__icon {
        width: 2.5rem;
    }

    &__label {
        font-size: 0.8rem;
        color: $white;
        letter-spacing: .08rem;
        font-weight: normal;
        margin-top: .8rem;
        text-align: center;
    }

    &--animated {
        flex-direction:column-reverse;
        width: 3.2rem;

        //Resume
        &:nth-child(2) {
            svg {
                width: 135% !important;
            }
        }

        .desktop-icon__label {
            margin-top: .2rem;
        }
    }
}