@import "../../scss/variables";
@import "../../scss/mixins";

.desktop {
    position:absolute;
    top: 4rem;
    right: 2.5rem;

    @media screen and (max-width: 500px) {
        right: 1.7rem;
    }

    &__icons {
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        @media screen and (max-height: 860px) {
            flex-direction: row;
            & > *:not(:last-child){
                margin-right: 3rem;
            }
        }
    }
}
