@import '../../scss/variables';
@import "../../scss/mixins";

.topBar {
    position: fixed;
    height: 2.5rem;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    background: $dark2;
    z-index: 5;
    box-shadow: $z-elevation-1;
    transform: translateY(-100%);

    &>div {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-left: 3rem;
        padding-right: 3rem;

        @include mediaQuery(0px, 610px) {
            padding-left: 1rem;
            padding-right: 1rem;
        }
    }

    &__brand {
        font-size: .9rem;
        font-weight: normal;
        user-select: none;
    }

    &__email {
        font-size: .8rem;
        margin-right: 1rem;
        transform: translateX(35%);
        opacity: 0;

        @include mediaQuery(0px, 780px) {
            display: none;
        }
    }

    &__time {
        margin-left: 1rem;
        font-size: .8rem;
        height: 1rem;
        font-weight: normal;
        padding-left: 1rem;
        user-select: none;
        //   transform: translateY(-.1rem);
    }
    &__sound,
    &__wifi,
    &__battery {
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 2;
    }

    &__sound {
        cursor: pointer;
        cursor: url("../../assets/images/cursor-pointer.png"), default;
    }

    &__wifi {
        width: 2.5rem;
        margin-right: -3rem;
    }

    &__battery {
        width: 7.5rem;
        margin-right: -4rem;
    }

    &__icons {
        margin-right: 2rem;
        z-index: 3;
        user-select: none;
    }
}