.noContent {
    margin: 1em auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;

    &__animation {
        position: relative;
        display: block;
        text-align: center;
        height: 100px;
        border-bottom: solid 2px #333;
        overflow: hidden;
        width: 80%;

        .bounce {
            display: inline-block;
            position: absolute;
            bottom: 0;
            left: 50%;
            width: 40px;
            height: 40px;
            margin-left: -20px;
            background-size: contain;
            animation: bounceAnim 1s cubic-bezier(.63, .09, .75, .46) infinite alternate,
                spinAnim 3s linear infinite;
        }

        .pebble:nth-of-type(2) {
            position: absolute;
            top: 95%;
            background-color: #333;
            width: 10px;
            height: 10px;
            border-radius: 20px 20px 0px 0px;
            animation: pebblesAnim 8s linear infinite;
        }

        .pebble:nth-of-type(3) {
            position: absolute;
            top: 97%;
            background-color: #333;
            width: 5px;
            height: 5px;
            border-radius: 10px 10px 0px 0px;
            animation: pebblesAnim 9s linear infinite;
        }

        .pebble:nth-of-type(4) {
            position: absolute;
            top: 98%;
            background-color: #333;
            width: 3px;
            height: 3px;
            border-radius: 20px 20px 0px 0px;
            animation: pebblesAnim 10s linear infinite;
        }
    }
}


@keyframes bounceAnim {

    0%,
    10% {
        bottom: 50%;
    }

    100% {
        bottom: 0%;
    }
}

@keyframes spinAnim {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes pebblesAnim {
    0% {
        right: -20%;
    }

    100% {
        right: 110%;
    }
}