@import "./scss/variables";
@import "./scss/mixins";


* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  position: relative;
  background-position: center;
  background-size: contain;
  transition: background-image 0.2s cubic-bezier(0.25,0.1,0.25,1);
  background-color: $orange;
  overflow: hidden;
  font-family: 'Roboto', sans-serif;
  color: $white;
  cursor: default;
  cursor: url("./assets/images/cursor-mouse.png"), default;
}

@include scrollbars(.5em, $dark2);
