@import '../../../scss/variables';


.topBar__icon {
    text-decoration: none;
    color: $white;
    margin-left: .6rem;
    cursor: pointer;
    cursor: url("../../../assets/images/cursor-pointer.png"), default;

    img {
        height: 1.2rem;
        transform: translateY(-.2px);
    }

    &.email {
        img {
            height: .9rem;
        }
    }

    &.linkedin {
        img {
            height: 1.2rem;
            transform: translateY(2px);
        }
    }

    &.github {
        img {
            height: 1rem;
        }
    }
}