@import "../../scss/variables";
@import "../../scss/mixins";


.window {
    position: absolute;
    padding-top: 24px;
    border-radius: 4px;
    min-width: 550px;
    height: 450px;
    background: linear-gradient(to right, #393836 0%, #43403e 100%);
    box-shadow: $z-elevation-3;
    z-index: 10;

    &:before {
        content: " ";
        position: absolute;
        z-index: 500;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        pointer-events: none;
        box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.15);
        border-radius: 3px;
    }

    &__controls {
        position: absolute;
        top: 0px;
        left: 0px;
        float: left;
        width:100%;
        height: 24px;
    }

    &__control {
        float: left;
        display: block;
        position: relative;
        width: 12px;
        height: 12px;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05), 0 0 0 1px rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        text-indent: -9999px;
        cursor: pointer;
        cursor: url("../../assets/images/cursor-pointer.png"), default;
        top:6px;
        left: 8px;


        &:hover:before,
        &:hover:after {
            visibility: visible;
        }

        &:before,
        &:after {
            position: absolute;
            z-index: 250;
            display: block;
            content: " ";
            visibility: hidden;
            width: 8px;
            height: 2px;
            top: 50%;
            left: 50%;
            background-color: rgba(0, 0, 0, 0.5);
        }

        &--close {
            background-color: #ee4f4b;

            &:before {
                transform: translate(-50%, -50%) rotate(45deg);
            }

            &:after {
                transform: translate(-50%, -50%) rotate(-45deg);
            }
        }
    }

    &__title {
        position: relative;

        span {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-70%, 2px);
            font-size: .9rem;
        }
    }

    &__content {
        width:100%;
        height: 100%;
        background-color: $white;
        color: $black;
        border: none;
        outline: none;
        vertical-align: top;
        cursor: default;
        border-radius: 0;
        overflow-y: scroll;
        overflow-x: hidden;
    }
}